.html {
  position: absolute;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Geometria', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Geometria";
  src: local("Geometria"),
    url("./fonts/geometria_medium.otf") format("opentype");
  font-weight: 500;
}
@font-face {
  font-family: "Geometria";
  src: local("Geometria"),
    url("./fonts/geometria_bold.otf") format("opentype");
  font-weight: 700;
}
